*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	font-family: $font-family-stem;
}

body {
	background: $color-alabaster-light;
}

.page-list {
	margin-top: 40px;
	margin-bottom: 40px;
	font-size: 18px;
	background: $color-white;
	thead {
		td {
			font-weight: 700;
			text-transform: uppercase;
		}
	}
	li {
		margin-bottom: 20px;
		&:last-of-type {
			margin-bottom: 0;
		}
	}
	a {
		color: $color-curious-blue;
		text-decoration: none;
	}
	td {
		padding: 20px;
		vertical-align: top;
		border: 1px solid $color-franch-gray;
		border-bottom: none;
		// &:first-of-type {
		// 	text-transform: uppercase;
		// 	font-weight: 700;
		// }
	}
	tr {
		&.question {
			background: $color-aquamarine;
		}

		&.not-ready {
			background: $color-geyser;
		}
		
		&:last-of-type {
			border-bottom: 1px solid $color-franch-gray;
		}
	}
}
// input
.input-unify {
	display: block;
	width: 100%;
	height: 60px;
	margin-bottom: 30px;
	padding: 24px 0 10px 20px;
	box-sizing: border-box;
	border-radius: 5px;
	border: 1px solid $color-mercury;
	color: $color-mine-shaft;
	font-size: 18px;
	line-height: 24px;
	background: $color-alabaster-light;
	&::placeholder {
		color: $color-silver;
		font-size: 18px;
	}
}

.input-file-unify {
	display: none;
}

.textarea-unify {
	display: block;
	width: 100%;
	height: auto;
	min-height: 260px;
	margin-bottom: 30px;
	padding: 20px;
	box-sizing: border-box;
	border-radius: 5px;
	border: 1px solid $color-mercury;
	color: $color-mine-shaft;
	font-size: 18px;
	line-height: 30px;
	background: $color-alabaster-light;
	&::placeholder {
		color: $color-silver;
		font-size: 18px;
	}
}

.label-unify {
	display: inline-block;
	margin-bottom: 14px;
	padding-left: 10px;
	color: $color-mine-shaft;
	font-size: 14px;
}

.label-file-unify {
	display: inline-block;
	font-size: 0;
}

.animation-unify {
	transition: all 0.33s ease;
}

// header 
.header {
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 9;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
	background-color: $color-white;
	&__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 100px;
	}

	&__logo {
		width: 157px;
		height: auto;
		margin-right: 30px;
		margin-left: 30px;
	}

	&__info {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-right: 90px;
	}

	&__login {
		margin-right: 40px;
	}

	&__name {
		font-size: 24px;
		font-weight: 700;
		color: $color-mine-shaft;
	}

	&__swither {
		font-size: 14px;
		color: $color-curious-blue;
		text-decoration: underline;
	}

	&__notice {
		position: relative;
		margin-right: 5px;
	}

	&__notice-quantity {
		position: absolute;
		top: -9px;
		right: -5px;
		width: 18px;
		height: 18px;
		background: $color-sunset-orange;
		font-size: 12px;
		color: $color-white;
		text-align: center;
		line-height: 18px;
		border-radius: 50%;
	}
	
	@media all and (max-width: 1024px){ 
		&__info {
			margin-right: 30px;
		}
	}

	@media all and (max-width: 768px){ 
		&__container {
			height: 60px;
		}
		
		&__logo {
			width: 100px;
			margin-left: 15px;
		}

		&__name {
			font-size: 18px;
		}

		&__login {
			margin-right: 15px;
		}

		&__info {
			margin-right: 15px;
		}
	}
}
// aside
.sidebar {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	z-index: 8;
	width: 250px;
	padding-top: 100px;
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.25);
	background: linear-gradient(to top, #243873, #1b96d1);
	
	&__menu {
		margin-top: 60px;
		margin-bottom: 0;
		padding-left: 0;
		list-style: none;
	}

	&__link {
		display: flex;
		align-items: center;
		height: 80px;
		padding-left: 30px;
		box-sizing: border-box;
		color: $color-periwinkle;
		text-decoration: none;
		cursor: pointer;
		
		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;	
			background-size: contain;		
		}
		
		.sponsor {
			background: url("../images/sponsor.svg") center no-repeat;			
			background-size: contain;
		}

		.handshake {
			background: url("../images/handshake.svg") center no-repeat;			
			background-size: contain;
		}

		.network {
			background: url("../images/network.svg") center no-repeat;			
			background-size: contain;
		}

		.dashboard {
			background: url("../images/dashboard.svg") center no-repeat;			
			background-size: contain;
		}

		&:hover,
		&.active {
			color: $color-white;
			background: $color-curious-blue;
			
			.dashboard {
				background: url("../images/dashboard_active.svg") center no-repeat;	
				background-size: contain;		
			}
			
			.sponsor {
				background: url("../images/sponsor_active.svg") center no-repeat;	
				background-size: contain;		
			}
			
			.handshake {
				background: url("../images/handshake_active.svg") center no-repeat;	
				background-size: contain;		
			}
			
			.network {
				background: url("../images/network_active.svg") center no-repeat;	
				background-size: contain;		
			}
		}
	}
	
	&__image {
		width: 32px;
		height: 32px;
		margin-right: 15px;
	}
	
	@media all and (max-width: 1024px){
		width: 60px;
		&__text {
			display: none;
		}

		&__link {
			padding-left: 14px;
		}

		&__menu {
			margin-top: 0;
		}
	}
	@media all and (max-width: 768px){
		padding-top: 60px;

		&__link {
			height: 65px;
		}
	}
}
.dashboard {
	position: relative;
	min-height: calc(100vh - 60px);
	&__container {
		width: 1920px;
		max-width: 100%;
		padding-top: 100px;
		padding-left: 305px;
		padding-right: 35px;
		box-sizing: border-box;
		&_without-sidebar {
			padding-left: 35px;
		}

		&_footer-padding {
			padding-bottom: 50px;
		}
	}	
	
	&__header {
		width: 100%;
		margin-top: 40px;
		margin-bottom: 40px;
		padding-right: 15px;
		padding-left: 15px;	
	}
	
	&__statistic,
	&__title {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 36px;
		font-weight: 700;
	}
	
	&__title {
		display: inline-block;
		margin-right: 30px;
	}

	&__edit {
		display: inline-block;
		color: $color-silver;
		font-size: 18px;
		font-weight: 700;
		text-decoration: none;

		&::before {
			display: inline-block;
			width: 8px;
			height: 8px;
			margin-right: 4px;
			content: '';
			background: url("../images/edit.png") center no-repeat;	
			background-size: contain;	
		}
	}

	&__item {
		min-height: 200px;
		margin-right: 15px;
		margin-bottom: 30px;
		margin-left: 15px;
		box-shadow: 4px 4px 40px rgba(29, 149, 209, 0.1);
		border-radius: 5px;
		background-color: $color-white;

	}

	&__section-header {
		width: calc(100% - 30px);
		margin: 40px 15px;
		color: $color-mine-shaft;
		font-size: 36px;
		font-weight: 700;
	}
	
	@media all and (max-width: 1024px){
		&__container {
			padding-left: 60px;
			padding-right: 0;
			&_without-sidebar {
				padding-left: 0;
			}
		}
	}

	@media all and (max-width: 768px){
		&__container {
			padding-top: 60px;
		}		
	}
	@media all and (max-width: 568px){
		&__title {
			font-size: 32px;
		}
	}
}
ul,
h3,
h2,
p {
	margin-top: 0;
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
}

.dashboard__row {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	&_first {
		flex-wrap: nowrap;
		margin-bottom: 30px;
	}

	&_second {
		flex-direction: column;
	}

	&_platform {
		padding-bottom: 50px;
	}

	&_financials {
		flex-wrap: wrap;
	}

	&_no-header {
		margin-top: 60px;
	}

	&_last {
		padding-bottom: 40px;
	}

	&.justify {
		justify-content: space-between;
	}

	&.center {
		justify-content: center;
	}

	@media all and (max-width: 1600px){
		flex-wrap: wrap;
		&_first {
			.dashboard__item {
				width: calc(33.3% - 30px);
			}
			
			.dashboard-brand {
				width: 100%;
			}
		}
	}

	@media all and (max-width: 1024px){
		&_first {
			.dashboard__item {
				padding: 40px 40px 25px;
			}
			
			.dashboard-brand {
				padding: 0;
			}
		}
	}

	@media all and (max-width: 768px){
		&_first {
			.dashboard__item {
				width: calc(50% - 30px);
				text-align: center;
			}
			.dashboard-brand {
				width: 100%;
				text-align: left;
			}
		}
	}

	@media all and (max-width: 568px){
		&_first {
			.dashboard__item {
				width: 100%;
				padding: 15px;
			}
			.dashboard-brand {
				padding: 0;
				&__content {
					padding: 15px;
				}
			}
		}
	}
}

.dashboard__column {
	@media all and (max-width: 1920px){
		display: flex;
		width: 100%;
	}
	@media all and (max-width: 1600px){
		flex-wrap: wrap;
	}
}

.dashboard-brand {
	display: flex;
	align-items: stretch;
	width: 760px;
	&__image {
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 200px;
		background: $color-alabaster;
	}

	&__content {
		padding: 40px;
	}

	&__header {
		margin-top: 0;
		margin-bottom: 20px;
		color: $color-curious-blue;
		font-size: 36px;
		font-weight: 700;
	}

	&__text {
		margin-top: 0;
		margin-bottom: 0;
		color: $color-mine-shaft;
		font-size: 18px;
	}
	
	@media all and (max-width: 1600px){
		width: 100%;
	}

	@media all and (max-width: 568px){ 
		flex-wrap: wrap;
		&__image {
			width: 100%;
			padding-top: 15px;
			padding-bottom: 15px;
		}
	}
}

.dashboard-foundation,
.dashboard-arrangements,
.dashboard-rating {
	padding: 40px 50px 25px 50px;
}

.dashboard-rating {
	width: 180px;
	min-width: 180px;
	text-align: center;
	&__header {
		margin-bottom: 30px;
		color: $color-mine-shaft;
		font-size: 18px;
		font-weight: 700;
	}

	&__text {
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

	@media all and (max-width: 1600px){
		min-width: 0;
		width: 200px;
	}

	@media all and (max-width: 568px){
		width: calc(100% - 30px);
	}
	
}

.dashboard-arrangements {
	width: 260px;
	min-width: 260px;
	&__header {
		margin-bottom: 15px;
		color: $color-franch-gray;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 18px;
	}
	
	@media all and (max-width: 1600px){
		min-width: 0;
	}

	@media all and (max-width: 1366px){ 
		width: calc(50% - 30px);
	}
	
	@media all and (max-width: 1024px){ 
		padding: 40px;
	}

	@media all and (max-width: 768px){ 
		text-align: center;
	}

	@media all and (max-width: 568px){ 
		width: 100%;
		padding: 15px;
	}
}

.dashboard-foundation {
	width: 260px;
	min-width: 260px;
	&__header {
		margin-bottom: 15px;
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-size: 18px;
	}

	@media all and (max-width: 1600px){
		min-width: 0;
	}

	@media all and (max-width: 1366px){ 
		width: calc(50% - 30px);
	}

	@media all and (max-width: 1024px){ 
		padding: 40px;
	}
	
	@media all and (max-width: 768px){ 
		text-align: center;
	}

	@media all and (max-width: 568px){ 
		width: 100%;
		padding: 15px;
	}
}

.dashboard-financials {
	position: relative;
	width: 760px;
	min-width: 760px;
	padding: 40px;
	&__header {
		margin-bottom: 40px;
		padding-right: 30px;
		padding-left: 30px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	} 
	
	&__chart {
		margin-bottom: 40px;
		img {
			max-width: 100%;
		}
	}
	
	&__values {
		display: flex;
		justify-content: space-between;
		padding-right: 30px;
		padding-left: 30px;
	}
	
	&__item {
		width: 33.3%;
		text-align: center;
	}

	&__spend {
		margin-bottom: 5px;
		color: $color-mine-shaft;
		font-size: 14px;
		font-weight: 300;
	}

	&__value {
		margin-bottom: 10px;
		color: $color-mine-shaft;
		font-size: 14px;
		font-weight: 700;
	}

	&__spons {
		color: $color-curious-blue;
		font-size: 18px;
		font-weight: 700;
	}

	&__chart-point {
		position: absolute;
		top: 143px;
		left: 288px;
		width: 8px;
		height: 8px;
		border-radius: 50%;
		background: $color-curious-blue;
		&.hidden {
			display: none;
		}
	}
	&__chart-description {
		position: absolute;
		top: 50px;
		left: 288px;
		width: 150px;
		height: 80px;
		padding: 15px 15px 15px  20px;
		box-shadow: 4px 4px 20px rgba(29, 149, 209, 0.1);
		border-radius: 5px;
		background-color: $color-white;
		&.hidden {
			display: none;
		}
	}

	&__chart-value-1 {
		margin-right: 10px;
		color: $color-curious-blue;
		font-size: 24px;
	}
	
	&__chart-value-2 {
		color: $color-sunset-orange;
		font-size: 12px;
	}
	
	&__chart-value-3 {
		color: $color-curious-blue;
		font-size: 14px;
		font-weight: 700;
	}
	@media all and (max-width: 1920px){
		flex-grow: 2;
	}
	@media all and (max-width: 1600px){
		width: 100%;
		min-width: 0;
		&__chart {
			text-align: center;
		}
	}
	@media all and (max-width: 768px){ 
		&__values {
			padding-right: 0;
			padding-left: 0;
		}
	}

	@media all and (max-width: 568px){ 
		padding: 15px;
		&__values {
			flex-wrap: wrap;
		}

		&__item {
			width: 50%;
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	@media all and (max-width: 425px){ 
		&__item {
			width: 100%;
		}
	}
}

.dashboard-sponsees {
	align-self: flex-start;
	width: 760px;
	padding: 40px 70px;
	&__header {
		margin-bottom: 20px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	}
	
	&__list {
		margin-top: 0;
		margin-bottom: 0;
		padding-left: 0;
		font-size: 0;
		list-style: none;
	}

	&__item {
		display: inline-block;
		width: 50%;
		min-width: 200px;
		margin-bottom: 15px;
		color: $color-gray;
		font-size: 14px;		
		&::before {
			display: inline-block;
			width: 14px;
			height: 10px;
			margin-right: 10px;
			content: '';
			background: url("../images/check.svg")
		}
	}
	@media all and (max-width: 1600px){
		width: 100%;
		padding: 40px;
		&__item {
			width: 25%;
		}
	}
	@media all and (max-width: 568px){
		padding: 15px;
		&__item {
			display: block;
			width: 100%;
			min-width: 0;
		}
	}
}

.dashboard-textbox {
	width: 760px;
	// max-width: calc(100% - 30px);
	padding: 40px 50px 60px 70px;
	&__header {
		margin-bottom: 24px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	}

	&__content {
		color: $color-gray;
		font-size: 14px;
		line-height: 23px;
	}

	@media all and (max-width: 1600px){
		width: 100%;
		padding: 40px;
	}

	@media all and (max-width: 568px){
		padding: 15px;
	}
}

.dashboard-statistic {
	width: 100%;
	margin-bottom: 40px;
	padding: 40px 70px;
	&__header {
		margin-bottom: 28px;
		color: $color-mine-shaft;
		font-size: 24px;
		font-weight: 700;
	}

	&__container {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		display: flex;
		width: 50%;
		margin-bottom: 40px;
	}

	&__chart {
		width: 210px;
		height: 170px;
		margin-right: 25px;
		text-align: center;
	}
	
	&__title {
		margin-bottom: 18px;
		color: $color-mine-shaft;
		font-size: 18px;
		font-weight: 700;
	}

	&__list {
		padding-left: 56px;
	}

	&__list-item {
		position: relative;
		margin-bottom: 10px;
		color: $color-gray;
		font-size: 14px;
		&::before {
			position: absolute;
			top: 50%;
			left: -15px;
			display: block;
			width: 8px;
			height: 8px;
			margin-top: -4px;
			border-radius: 50%;
			content: '';
		} 
	
		&_curious-blue::before {
			background: $color-curious-blue; 
		}
	
		&_sunset-orange::before {
			background: $color-sunset-orange; 
		}
	
		&_heliotrope::before {
			background: $color-heliotrope; 
		}
	
		&_aquamarine::before {
			background: $color-aquamarine; 
		}
	}
	@media all and (max-width: 1366px){
		&__item {
			width: 100%;
		}
	}
	@media all and (max-width: 1024px){ 
		margin-bottom: 30px;
		padding: 40px;
		
		&__chart {
			width: 170px;
			min-width: 170px;
		}
	}
	@media all and (max-width: 568px){
		padding: 15px;
		&__item {
			flex-wrap: wrap;
			justify-content: center;
		}

		&__chart {
			margin-right: 0;
			margin-bottom: 30px;
			&:last-of-type {
				margin-bottom: 0;
			}
		}

		&__info {
			width: 100%;
		}
	} 
}

.dashboard-platform-section {
	flex-wrap: wrap;
	padding-bottom: 50px;
	&__header {
		width: 100%;
		margin-right: 15px;
		margin-top: 40px;
		margin-bottom: 40px;
		margin-left: 15px;
		color: $color-mine-shaft;
		font-size: 36px;
		font-weight: 700;
	}
}

.dashboard-platform {
	display: flex;
	align-items: stretch;
	width: calc(33.33% - 30px);
	padding: 40px 70px;
	&__content {
		margin-right: 70px;
	}

	&__value {
		margin-bottom: 24px;
		color: $color-curious-blue;
		font-size: 48px;
		font-weight: 700;
	}

	&__text {
		color: $color-gray;
		font-family: Stem;
		font-size: 18px;
	}

	&__image {
