			flex-direction: column;
			align-items: center;
			padding-right: 15px;
			padding-left: 15px;
		}
		&__metric {
			margin-right: 0;
			margin-bottom: 30px;
			text-align: center;
			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
